import React from "react";
import axios from "axios";
import { Route, Switch, withRouter } from "react-router";
import PrivateRoute from "./PrivateRoute";
import "./App.css";
import Header from "./components/Header";
import Home from "./components/home/Home";
import Footer from "./components/Footer";
import Register from "./components/auth/Register";
import Login from "./components/auth/Login";
import UsersView from "./components/UsersView";
import Services from "./components/home/Services";
import Account from "./components/Account";
import Rating from "./components/Rating";
import { config, API_ROUTES } from "./constants";
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      authenticated: false,
      currentUser: "",
      error: ""
    };
  }

  componentDidMount() {
    this.getAllUsers();
    // window.localStorage.getItem("authenticated"),
    // window.localStorage.getItem("currentUser"),
    this.setState({
      authenticated: window.localStorage.getItem("authenticated"),
      currentUser: JSON.parse(window.localStorage.getItem("currentUser"))
    });
  }

  getAllUsers = () => {
    axios
      .get(config.API_URL + API_ROUTES.users)
      .then(res => res.data)
      .then(data => {
        this.setState({ users: data });
      })
      .catch(error => {
        console.log(error.message);
        this.setState({ error: error.message });
      });
  };
  handleLogin = userdata => {
    const { username, password } = userdata;
    axios
      .post(config.API_URL + API_ROUTES.login, {
        username: username,
        password: password
      })
      .then(res => {
        this.getAllUsers();
        console.log(res);
        this.setState({ authenticated: true, currentUser: res.data });
        window.localStorage.setItem("authenticated", true);
        window.localStorage.setItem("currentUser", JSON.stringify(res.data));
      })
      .then(() => {
        this.props.history.push("/users");
      })

      .catch(error => {
        console.log(error);
        console.log(error.response);
        this.setState({ error: error.response });
        // removied .datta
      });
  };

  handleLogout = () => {
    this.setState({ authenticated: false });
    window.localStorage.removeItem("authenticated");
    window.localStorage.removeItem("currentUser");
    this.props.history.push("/");
  };

  handleSignUp = userData => {
    axios
      .post(config.API_URL + API_ROUTES.register, userData)
      .then(res => {
        console.log(res);
        // this.setState({ authenticated: true });
        // this.getAllUsers();
        // this.props.history.push("/users");
      })
      .then(() => {
        this.handleLogin(userData);
      })

      .catch(error => {
        console.log(error);
      });
  };

  render() {
    const { users, authenticated, currentUser, error } = this.state;

    return (
      <div>
        <Header
          handleLogout={this.handleLogout}
          authenticated={authenticated}
          currentUser={currentUser}
        />

        {error && (
          <div className="mt-5 text-center alert alert-danger">{error}</div>
        )}

        <Switch>
          <Route exact path="/">
            <Home users={users} />
          </Route>
          <Route path="/register">
            <Register handleSignUp={this.handleSignUp} />
          </Route>
          <Route path="/login">
            <Login handleLogin={this.handleLogin} error={error} />
          </Route>

          <Route path="/users">
            <UsersView currentUser={currentUser} users={users} />
          </Route>
          <PrivateRoute path="/rating">
            <Rating />
          </PrivateRoute>
          <PrivateRoute path="/services">
            <Services />
          </PrivateRoute>
          <PrivateRoute path="/account">
            <Account currentUser={currentUser} />
          </PrivateRoute>

          <Route path="/account">
            <Rating />
          </Route>
        </Switch>

        <Footer />
      </div>
    );
  }
}

export default withRouter(App);
